body {
    font: 14px 'Century Gothic', Futura, sans-serif;
    margin: 20px;
}
.app {
    width: 500px;
    margin: 0 auto;
    padding: 40px; 
    min-height: 470px;
    border: 1px solid #ddd
}
ol, ul {
    padding-left: 30px;
}
.board-row:after {
    clear: both;
    content: '';
    display: table;
}
.status {
    margin-bottom: 10px;
    font-size: 20px;
    font-family: fira code;
}
.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 34px;
    font-weight: bold;
    line-height: 34px;
    height: 50px;
    width: 50px;
    margin-top: -1px;
    margin-right: -1px;
    padding: 0;
    text-align: center;
}

.square:focus {
    outline: none;
}
.kbd-navigation .square:focus {
    background: #ddd;
}
.game {
    display: flex;
    flex-direction: row;
}
.game-info {
    margin-left: 20px;
}
.move-btn {
    background: #fff;
    border: 1px solid #ddd;
    padding: 4px;
    border-radius: 3px;
    margin-bottom: 4px;
}